              :footer-props="{
                  itemsPerPageText: 'Registos por pgina'
               }"
            >
              <template v-slot:item.action="{ item }">
                <v-btn
                v-if="canEdit()"
                  color="primary"
                  x-small
                  outlined
                  rounded
                  class="text-none mr-1"
                  @click="updatePermissionModal(item.id)"
                >editar</v-btn>
                <v-btn
                v-if="canRemove()"
