  </div>
</template>

<script>
import AddPermission from "./Create";
import UpdatePermission from "./Update";
import { flashAlert, actionAlert } from "@/mixins/AppAlerts";
import { getDatas, getData, deleteData } from "@/mixins/SendForm";

export default {
  mixins: [flashAlert, actionAlert, getData, getDatas, deleteData],

  data() {
    return {
      umteste: 3,
      fab: false,

      search: "",
      permissions_id: [],
      headers: [
        {
          text: "Nome",
          value: "name"
        },
        {
          text: "Operao",
          align: "center",
          sortable: false,
          value: "action"
        }
      ],
      permission: []
    };
  },

  computed: {
    permissions: function() {
      return this.$store.getters.permissions;
    }
  },

  created: function() {
    this.getAll(this.permissions, "getPermissions");
    window.getApp.$on("APP_UPDATE_ALL_PERMISSIONS_DATA", () => {
      this.refresh("getPermissions");
    });
  },

  components: {
    AddPermission,
    UpdatePermission
  },

  methods: {
    addPermissionModal() {
      window.getApp.$emit("APP_ADD_PERMISSION_MODAL");
    },

    updatePermissionModal(id) {
      this.permission = this.getSingle("permissions", id, this.permissions);
      window.getApp.$emit("APP_UPDATE_PERMISSION_MODAL", this.permission);
    }
  }
};
</script>

