export const imageFilesRules = {
    data() {
        return {
            coverRules: [
                value =>
                    !value ||
                    value.size < 2000000 ||
                    "Uma imagem de capa no pode ter um tamanho superior a 3MB"
            ],

            logoRules: [
                value =>
                    !value ||
                    value.size < 1000000 ||
                    "Um logo tipo no pode ser superior a 1MB"
            ],
        };
    },
};